@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes moveForward {
  0% {
    transform: translateX(40);
  }
  50% {
    transform: translateX(70px); /* Move icons 20px to the right */
  }
  100% {
    transform: translateX(30); /* Return to original position */
  }
}

@layer utilities {
  .animate-moveForward {
    animation: moveForward 5s ease-in-out infinite;
  }
}
